pre code {
  display: block;
  overflow-x: auto;
  padding: 1em;
  -moz-tab-size: 2em;
    -o-tab-size: 2em;
       tab-size: 2em;
  border: 1px solid #aaa;
  background-color: #fff;
  border-radius: 3px;
}

ul, ol {
  -webkit-padding-start: 2rem;
          padding-inline-start: 2rem;
}

code {
  color: #3182bd;
  font-weight: 400;
  -webkit-hyphens: manual;
      -ms-hyphens: manual;
          hyphens: manual;
  word-break: initial;
  overflow-wrap: normal;
}

.alert {
  position: relative;
  padding-top: 1rem;
  padding-bottom: 1rem;
  padding-right: 2rem;
  padding-left: calc(2rem - 4px);
  border-left: 4px solid;
  margin-bottom: 1rem;
  border-radius: 3px;
}

.box.box-pagehint {
  padding-left: 16px;
  border-left: 4px solid #b31b34;
}

.alert-info {
  background-color: #e0faff;
  border-left-color: #3182bd;
}

.alert-danger {
  background-color: #ffe3e7;
  border-left-color: #b31b34;
}

